.App {
  text-align: center;
  color: #e7e7e7;
}

.conteiner {
  width: 300px;
  margin: auto;
  border-radius: 10px;
  box-shadow: -0px 15px 25px -5px #555;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.pantalla {
  width: 101%;
  height: 40px;
  outline: none;
  border: none;
  padding: 2px;
  background-color: #23242a;
  color: #e7e7e7;
}

.calc {
  background-color: #23242c;
  padding: 10px;
}

button {
  background-color: #444;
  width: 66px;
  border: none;
  padding: 30px;
  color: #e7e7e7;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #333;
}
